@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
/*
  import font
*/
/*
  Declare variables for font color and text sizes
*/
:root {
  --font-color: #030303;
  --background-color: #fcfcfc;
  --nav-background-color: #ffffff;
  --input-background-color: #fcfcfc;
  --input-border-color: #999;
  --input-text-color: #030303;
  --error-color: red;
  --error-bg: #ff000011;
  --accent-color: #4b4486;
  --accent-color-light: #4b448655;
  --h1-font-size: clamp(21px, 4.4vmin, 32px);
  --h2-font-size: clamp(18px, 3.8vmin, 26px);
  --h3-font-size: clamp(16px, 3vmin, 22px);
  --body-text-size: clamp(14px, 2.6vmin, 16px);
  --default-font-weight: 200;
}
/*
  reset
*/
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  background-color: #fcfcfc;
  background-color: var(--background-color);
}
/* 
  default main styling
*/
main {
  width: min(84vw, 900px);
  margin: 0 auto;
  padding-bottom: 80px;
}
main > div {
  margin: clamp(14px, 2.6vmin, 16px) 0;
  margin: var(--body-text-size) 0;
}
/*
  general image styling
*/
img {
  width: 100%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-filter: drop-shadow(
    0px 0px 4px #4b448655);
          filter: drop-shadow(
    0px 0px 4px #4b448655);
  -webkit-filter: drop-shadow(
    0px 0px 4px var(--accent-color-light)
  );
          filter: drop-shadow(
    0px 0px 4px var(--accent-color-light)
  );
}
/* 
  button styling
*/
button {
  font-family: 'Montserrat', sans-serif;
  opacity: 0.9;
  border: 1px solid #666;
  font-weight: 200;
  padding: 5px 12px;
  height: calc(clamp(14px, 2.6vmin, 16px) * 1.5 + 10);
  height: calc(var(--body-text-size) * 1.5 + 10);
  border-radius: clamp(14px, 2.6vmin, 16px);
  border-radius: var(--body-text-size);
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  max-width: 30ch;
  margin-top: clamp(21px, 4.4vmin, 32px);
  margin-top: var(--h1-font-size); margin-bottom: clamp(21px, 4.4vmin, 32px); margin-bottom: var(--h1-font-size);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
button:hover {
  opacity: 1;
}
button:first-of-type:not(:last-of-type), button + button:not(:last-of-type) {
  margin-right: 0;
  border-right: 1px solid #333;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
button + button {
  margin-left: 0;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.fake-button {
  margin-top: clamp(18px, 3.8vmin, 26px);
  margin-top: var(--h2-font-size);
  margin-bottom: clamp(18px, 3.8vmin, 26px);
  margin-bottom: var(--h2-font-size); 
  text-decoration: underline;
  text-align: center;
  cursor: pointer;
}
/* basic input styling */
input {
  background-color: #fcfcfc;
  background-color: var(--input-background-color);
  color: #030303;
  color: var(--input-text-color);
  border : 1px solid #999;
  border : 1px solid var(--input-border-color);
  padding: 5px;
  border-radius: 7px;
  outline: 0;
}
input:focus {
  border-color: #030303;
  border-color: var(--input-text-color);
}
input::-webkit-input-placeholder {
  color: #999;
  color: var(--input-border-color);
}
input:-ms-input-placeholder {
  color: #999;
  color: var(--input-border-color);
}
input::-ms-input-placeholder {
  color: #999;
  color: var(--input-border-color);
}
input::placeholder {
  color: #999;
  color: var(--input-border-color);
}
/*
  helpers
*/
.grid {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr;
  max-width: 100%;
}
.grid > * {
  width: 100%;
}
.none {
  display: none !important;
}
/* loading animation - screen freeze */
#loading-animation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: #666;
  opacity: 0.4;
}
/* 
  dont show attributions by default
*/
#attributions {
  display: none;
}
/* 
  registration/login page styling
*/
.phone-number-input {
  display: grid;
  align-items: center;
  justify-items: center;
}
.text-center {
  text-align: center;
}
/*
  import font
*/
/*
  global typography settings
*/
* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  color: var(--font-color);
  font-weight: var(--default-font-weight);
}
/* 
  general header and heading style
*/
header {
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
}
/* 
  Concept:
    h[1-3] are desktop
    h[4-6] are mobile
    Same font-size and color, but uses media query to change display
*/
h1, h2, h3, h4, h5, h6 {
  display: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
@media (min-width: 1100px) {
  h1.long {
    display: block;
    text-align: center;
    font-size: var(--h1-font-size);
    font-weight: calc(var(--default-font-weight) + 100);
    text-decoration: underline;
  }
  h2.long {
    display: block;
    text-align: center;
    font-size: var(--h2-font-size);
  }
  h3.long {
    display: block;
    text-align: center;
    font-size: var(--h3-font-size);
  }
}
@media (max-width: 1099px) {
  h1.short {
    display: block;
    text-align: center;
    font-size: var(--h1-font-size);
    font-weight: calc(var(--default-font-weight) + 100);
    text-decoration: underline;
  }
  h2.short {
    display: block;
    text-align: center;
    font-size: var(--h2-font-size);
  }
  h3.short {
    display: block;
    text-align: center;
    font-size: var(--h3-font-size);
  }
}
b {
  font-weight: 500;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.error {
  color: var(--error-color);
  text-align: center;
  text-decoration: underline;
  margin: var(--h1-font-size);
}
/* accent color stuff */
h1, h4, b {
  color: var(--accent-color);
}
svg {
  --color-1: #4b4486;
  --color-2: #CA054D;
  --color-3: #A4D4B4;
  --color-4: #ffcf9c;
}
@-webkit-keyframes slideUp {
  0% {
    transform: translateY(-5%) scaleY(0.05);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes slideUp {
  0% {
    transform: translateY(-5%) scaleY(0.05);
  }
  100% {
    transform: scaleY(1);
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/* dashboard html styling */
div.dashboard {
  width: min(94%, 1000px);
  margin: 25px auto;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2fr 1fr;
}
div.dashboard > :first-child {
  grid-column: 1 / 3;
}
div.dashboard > figure {
  width: min(100%, 750px);
}
div.dashboard img {
  width: min(100%, 32px);
}
/* svg styling */
text {
  font-size: 18px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  fill: var(--font-color);
}
.time-of-day rect {
  transform-origin: bottom;
  -webkit-animation: slideUp 1.5s;
          animation: slideUp 1.5s;
  fill: var(--accent-color);
}
.time-of-day line {
  stroke: var(--accent-color);
}
div.dashboard > figure {
  margin: 0;
}
div.dashboard > figure > figcaption {
  font-size: var(--h3-font-size);
  text-align: center;
}
g.circle-graph-group > path + text {
  opacity: 0;
}
g.circle-graph-group > path:hover + text {
  opacity: 1;
}
.type-of-entry > .legend {
  fill: #ffffff;
}
.type-of-entry path {
  transition: 0.4s;
}
.type-of-entry path:hover {
  transform: scale(1.07);
}
text.trendline-1 {
  stroke: var(--color-1);
}
text.trendline-2 {
  stroke: var(--color-2);
}
text.trendline-3 {
  stroke: var(--color-3);
}
text.trendline-4 {
  stroke: var(--color-4);
}
line.trendline-1 {
  stroke: var(--color-1);
}
line.trendline-2 {
  stroke: var(--color-2);
}
line.trendline-3 {
  stroke: var(--color-3);
}
line.trendline-4 {
  stroke: var(--color-4);
}
/* specific to keywords styling */
.keyword-container {
  display: grid;
  align-items: center;
  justify-items: center;
  height: 0;
  overflow: visible;
}
.keyword-search {
  transform: translateY(1vw);
  display: grid;
  grid-template-columns: calc(0.8 * var(--body-text-size)) 1fr calc(0.8 * var(--body-text-size));
  height: calc(1.8 * var(--body-text-size));
  padding: 0;
  border: 1px solid var(--input-border-color);
  border-radius: var(--body-text-size);
  background-color: var(--input-background-color);
  width: min(100%, 22ch);
  padding: 0 2.5%;
}
.keyword-search * {
  margin: 0;
  padding: 0;
}
.keyword-search button {
  border: none;
  outline: none;
  margin: none;
  padding: none;
  display: grid;
  align-items: center;
  justify-items: center;
  width: calc(0.8 * var(--body-text-size));
}
.keyword-search button img {
  width: calc(0.75 * var(--body-text-size));
}
.keyword-search input {
  grid-column: 2;
  border: none;
  width: 80%;
}
.bar-chart-bar > rect:first-child {
  fill: var(--color-3);
}
.bar-chart-bar > rect:nth-child(2) {
  fill: var(--color-2);
}
.bar-chart-bar > rect:nth-child(3) {
  fill: var(--color-1);
}
.bar-chart-text > text:first-child {
  stroke: var(--color-1);
}
.bar-chart-text > text:nth-child(2) {
  stroke: var(--color-2);
}
.bar-chart-text > text:nth-child(3) {
  stroke: var(--color-3);
}
/* reset the dashboard layout when on smaller devices */
@media (max-width: 1100px) {
  div.dashboard {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 25px;
    gap: 25px;
  }
  div.dashboard > :first-child {
    grid-column: 1;
  }
  text {
    font-size: 22px;
  }
}
/* 
  navigation bar styling
  including media query
*/
nav {
  position: fixed;
  display: grid;
  align-items: center;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 72px;
  padding: 12px 0;
  border-top: 1px solid var(--font-color);
  background-color: var(--nav-background-color);
}
nav > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-gap: 14px;
  gap: 14px;
  grid-template-columns: 1fr repeat(5, 48px) 1fr;
  align-items: center;
  justify-items: center;
}
nav > ul > li {
  height: 40px;
  width: 48px;
  display: grid;
  grid-template-rows: 28px 12px;
  justify-items: center;
  align-items: baseline;
  font-size: 10px;
  line-height: 12px;
}
nav > ul > li > img {
  height: 28px;
  width: 28px;
  transition: 0.3s;
}
nav > ul > li.active > label {
  text-decoration: underline;
}
@media (min-width: 1100px) {
  nav {
    width: 64px;
    height: 100vh;
    top: 0;
    align-items: baseline;
    border-top: 0;
    border-right: 1px solid var(--font-color);
  }
  nav > ul {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, 48px) 1fr;
  }
}

main.settings div {
  margin: 0;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
  overflow: hidden;
  width: 100%;
}
main.settings > div {
  padding: 32px 0;
}
main.settings > div:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}
main.settings > div > *:last-child, main.settings > div > *:first-child {
  margin: 0;
}
main.settings > div > *:not(:first-child):not(:last-child) {
  margin: 7px;
}
main.settings > div.action > span {
  text-decoration: underline;
  cursor: pointer;
}
main.settings > div.action > span:not(:last-child) {
  margin-bottom: 12px;
}
.setting {
  display: grid;
  grid-template-columns: 1fr 64px;
}
.setting > *:first-child {
  justify-content: left;
}
.setting > *:nth-child(2) {
  justify-items: right;
}
.setting-group > h3 {
  margin: var(--h1-font-size);
  text-decoration: underline;
}
.setting-group > .setting {
  margin: 18px;
}
.toggle {
  display: inline-block;
  width: 48px;
  height: 24px;
  border-radius: 12px;
  border: 2px solid #666;
  transition: 0.5s;
}
.toggle::after {
  content: "";
  width: 14px;
  height: 14px;
  border-radius: 7px;
  background-color: #666;
  position: relative;
  top: 3px;
  display: block;
  right: -4px;
  transition: 0.1s;
}
.toggle.on {
  border-color: var(--accent-color);
}
.toggle.on::after {
  background-color: var(--accent-color);
  right: -26px;
}
/*
  writing - question-answer styling
*/
*[contenteditable] {
  color: var(--input-text-color);
  outline: none;
}
*[contenteditable]:focus {
  border-bottom: 1px dashed var(--accent-color);
}
.freeform {
  padding: 4px 6px;
}
.questions {
  margin: 2vw;
}
.questions > div {
  margin-top: 7px;
  padding: 3px 5px;
  text-align: left;
}
.questions > div::before {
  content: "";
  box-shadow: none;
  outline: none;
  position: absolute;
  margin-left: -24px;
  margin-top: -25px;
  width: var(--body-text-size);
  height: var(--body-text-size);
  background-size: cover;
  background-image: url(data:image/png;base64,);
  -webkit-filter: drop-shadow(
    0px 0px 2px #ffffff
  );
          filter: drop-shadow(
    0px 0px 2px #ffffff
  );
}
.questions:focus > div::before {
  outline: none;
  box-shadow: none;
}
.questions > div:not(:last-child) {
  margin-bottom: 24px;
}
/* Add Question Dialog styling */
.current-question-list {
  list-style-type: none;
  margin: var(--h1-font-size);
  padding: 0;

}
.current-question-list > li {
  padding: 5px 12px;
}
.add-question-input {
  padding: 5px 12px;
}
/* 
  journal styling
*/
@-webkit-keyframes header-slide-in {
  0% {
    opacity: 0;
    transform: translateX(-250px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes header-slide-in {
  0% {
    opacity: 0;
    transform: translateX(-250px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
article {
  -webkit-animation: header-slide-in 0.6s;
          animation: header-slide-in 0.6s;
  min-height: 90px;
  display: grid;
  align-items: center;
}
.entry-header {
  grid-template-columns: 1fr 4fr 1fr;
}
section:not(:last-of-type) {
  margin-bottom: 24px;
}
section.journal-entry {
  padding: 0 14px;
}
section.journal-entry > header {
  display: grid;
  grid-template-columns: 14px 1fr 14px;
}
section.journal-entry.to-remove {
  background-color: var(--error-bg);
}
section.journal-entry.to-remove > header {
  grid-template-columns: 32px 1fr 32px;
}
section.journal-entry.to-remove > header > span {
  display: grid;
  grid-template-columns: 14px 6px 14px;
}
section > ul {
  font-weight: var(--default-font-weight);
  font-size: var(--body-text-size);
  list-style-type: none;
  margin: 0;
  padding: 0;
}
section > ul > li {
  line-height: 21px;
  letter-spacing: 0.5px;
}
.pill {
  display: grid;
  justify-items: right;
}
.pill > span {
  text-align: center;
  width: 3ch;
  border: 1px solid var(--font-color);
  background-color: var(--background-color);
  border-radius: 6px;
}
section.journal-entry figure {
  text-align: center;
}
.skill-category {
  width: min(84vw, 600px);
}

.skill-category li {
  display: grid;
  padding: 14px 8px;
  justify-content: left;
  justify-items: left;
}

.skill-category li:not(:first-child) {
  border-top: 1px dashed var(--font-color);
}

.skill-category li.stable {
  grid-template-columns: 1fr 14px;
}

.skill-category li.to-remove {
  background-color: var(--error-bg);
  grid-template-columns: 1fr 36px;
}
.skill-category li.to-remove > span > img {
  width: 14px;
  display: inline;
}
.skill-category li.to-remove > span > img:first-child {
  margin-right: 8px;
}
div.grid.search-container {
  grid-template-columns: 28px 1fr 28px;
}
div.grid.search-container > form.search-bar {
  grid-column: 2;
  background-color: var(--background-color);
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr calc(var(--body-text-size) * 2);
  padding: 0 var(--body-text-size);
  width: 67%;
  height: calc(var(--body-text-size) * 2);
  border: 1px solid var(--input-border-color);
  border-radius: var(--body-text-size);
}
div.grid.search-container > form.search-bar > input {
  background-color: var(--background-color);
  height: calc(var(--body-text-size) * 1.5);
  width: 100%;
  border: 0;
  outline: 0;
  left: calc(var(--body-text-size) / 2);
}
div.grid.search-container > form.search-bar > img {
  width: var(--body-text-size);
  height: var(--body-text-size);
}
div.grid.search-container > span.date-filter {
  cursor: pointer;
  width: 22px;
  height: 22px;
}

/* for the dt filter */
div.grid.search-filter {
  grid-template-columns: 1fr 30ch 1fr;
  grid-gap: 12px;
  gap: 12px;
}
div.grid.search-filter > div.date-start, div.grid.search-filter > div.date-end {
  grid-column: 2;
  grid-template-columns: 5ch 25ch;
}
div.grid.search-filter > div.date-start > div, div.grid.search-filter > div.date-end > div {
  grid-template-columns: 6ch 2ch 6ch 2ch 9ch;
}
/* list that helps add filtes */
ul.add-filter-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  grid-gap: 12px;
  gap: 12px;
  grid-template-columns: 1fr repeat(2, 32px) 1fr;
}
ul.add-filter-list > :first-child {
  grid-column: 2;
}
.filter {
  grid-gap: 14px;
  gap: 14px;
  grid-template-columns: 32px 1fr 3fr 32px;
}
.filter > span {
  text-align: center;
}
.time-filter > div {
  display: grid;
  align-items: center;
  text-align: center;
  grid-template-columns: 1fr 6ch 2ch 6ch 4ch 1fr;
}
.date-filter > div {
  display: grid;
  align-items: center;
  text-align: center;
  grid-template-columns: 1fr 6ch 2ch 6ch 2ch 8ch 1fr;
}
.filter > div > :first-child {
  grid-column: 2;
}
/* list that holds the filters */
ol.filter-list {
  margin: 0;
  padding: 0;
}
.grid.switch-mode {
  grid-template-columns: 1fr 6ch 12px 1fr;
}
.grid.switch-mode > :first-child {
  grid-column: 2;
}
@media (max-width: 1100px) {
  .date-filter, .time-filter {
    padding: 24px 0;
    grid-template-columns: 36px 1fr;
    grid-template-rows: 32px 32px;
  }
  .filter-list > li:not(:last-child) {
    border-bottom: 1px dashed var(--font-color);
  }
  .date-filter > :first-child, .time-filter > :first-child {
    grid-column: 1;
    grid-row: 1;
  }
  .date-filter > :nth-child(2), .time-filter > :nth-child(2) {
    grid-column: 1;
    grid-row: 2;
  }
  .date-filter > :nth-child(3), .time-filter > :nth-child(3) {
    grid-column: 2;
    grid-row: 1;
  }
  .date-filter > :nth-child(4), .time-filter > :nth-child(4) {
    grid-column: 2;
    grid-row: 2;
    text-align: right;
  }
  .time-filter > div {
    grid-template-columns: 1fr 4ch 2ch 4ch 4ch;
  }
  .date-filter > div {
    display: grid;
    align-items: center;
    text-align: center;
    grid-template-columns: 1fr 4ch 2ch 4ch 2ch 4ch;
  }
  .grid.switch-mode {
    justify-items: left;
    text-align: left;
    grid-template-columns: 6ch 12px 1fr;
  }
  .grid.switch-mode > :first-child {
    grid-column: 1;
  }
}
.add-filter-disabled {
  opacity: 0.3;
}
.recorder-container span {
  display: inline-block;
  height: 9ch;
  width: 9ch;
  border-radius: 50%;
  line-height: 9ch;
  text-align: center;
  border: 1px solid var(--font-color);
}
